:root {
    --app-bg-dark: rgb(24, 23, 32);
}

body {
    background-color: var(--app-bg-dark);
    /* color: #e4e5e6; */
}

.container{
    width: min(100% - 30px, 1080px);
    margin-inline: auto;
}

main {
    padding: 60px;
}

section {
    margin-top: 60px;
}

h1, h2, h3, p, li, a, input {
    color: #fff;
}

h2 {
    font-size: 2.2rem;
}

p {
    margin-top: 15px;
    font-size: 1.8rem;
}

main 
{
    height: 100svh;
    display:  grid;
    place-items: center;

   div{
        text-align: center;
    } 

    h2 {
        font-size: 4rem;
        font-weight: 400;
        line-height: 1;

        span {
            color: #00f3b6;
        }
    }
}

input{
    width: 300px;
    height: 44px;
    margin-top: 40px;
    padding: 0 20px;
    border-radius: 50px;
    border: 1px solid rgb(255 255 255 / 5%);
    background-color: rgb(255 255 255 / 5%);
    font-size: 1.8rem;
    text-align: center;
}
